<template>
    <el-container>
        <el-header>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button type="info" @click="back()">返回</el-button>
        </el-header>
        <el-main>

            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="200px" class="demo-ruleForm">
                <el-form-item label="商品封面" prop="cover">
                    <el-upload action="" drag accept="image/jpeg,image/gif,image/png"
                               :auto-upload="false" :show-file-list="false" :on-change='changeUpload0'>
                        <img :src="ruleForm.cover" class="pic">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">点击上传</div>
                    </el-upload>
                </el-form-item>
                <el-form-item label="商品名称" prop="name">
                    <el-input placeholder="请输入商品名称" v-model="ruleForm.name" maxlength="400"></el-input>
                </el-form-item>
                <el-form-item label="商品库存" prop="sortIndex">
                    <el-input-number v-model="ruleForm.stock" :min="1" :max="999999" label="请输入商品库存"
                                     style="width: 300px"></el-input-number>
                </el-form-item>
                <el-form-item label="商品价格" prop="price">
                    <el-input-number v-model="ruleForm.price" :min="1" :max="999999" :precision="2" label="请输入商品价格"
                                     style="width: 300px"></el-input-number>
                </el-form-item>
                <el-form-item label="商品状态" prop="state">
                    <el-select v-model="ruleForm.state" style="width: 300px">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="商品分类" prop="classifyProducts">
                    <el-select v-model="ruleForm.classifyProducts" multiple style="width: 300px">
                        <el-option
                                v-for="item in classifyOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="商品轮播图">
                    <div class="formDiv">
                        <el-upload action="" drag accept="image/jpeg,image/gif,image/png"
                                   :auto-upload="false" :show-file-list="false" :on-change='changeUpload1'>
                            <img :src="ruleForm.imgSrc1" v-show="pic1Show" class="pic1">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">点击上传</div>
                        </el-upload>
                        <el-upload action="" drag accept="image/jpeg,image/gif,image/png"
                                   :auto-upload="false" :show-file-list="false" :on-change='changeUpload2'>
                            <img :src="ruleForm.imgSrc2" v-show="pic2Show" class="pic1">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">点击上传</div>
                        </el-upload>
                        <el-upload action="" drag accept="image/jpeg,image/gif,image/png"
                                   :auto-upload="false" :show-file-list="false" :on-change='changeUpload3'>
                            <img :src="ruleForm.imgSrc3" v-show="pic3Show" class="pic1">
                            <i class="el-icon-upload"></i>
                            <div class="el-upload__text">点击上传</div>
                        </el-upload>
                    </div>
                </el-form-item>

                <el-form-item label="商品描述" prop="contentHtml">
                    <vue-ueditor-wrap v-model="ruleForm.contentHtml" :config="myConfig"></vue-ueditor-wrap>
                </el-form-item>
            </el-form>

        </el-main>

        <!-- vueCropper 剪裁图片实现-->
        <el-dialog title="剪裁图片" :visible.sync="dialogVisible" append-to-body>
            <div class="cropper-content">
                <div class="cropper" style="text-align:center">
                    <vueCropper
                            ref="cropper"
                            :img="option.img"
                            :outputSize="option.size"
                            :outputType="option.outputType"
                            :info="true"
                            :full="option.full"
                            :canMove="option.canMove"
                            :canMoveBox="option.canMoveBox"
                            :original="option.original"
                            :autoCrop="option.autoCrop"
                            :fixed="option.fixed"
                            :fixedNumber="option.fixedNumber"
                            :centerBox="option.centerBox"
                            :infoTrue="option.infoTrue"
                            :fixedBox="option.fixedBox"
                            @realTime="realTime"
                    ></vueCropper>
                </div>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取消</el-button>
                <el-button type="primary" @click="finish" :loading="loading">确定</el-button>
            </div>
        </el-dialog>
    </el-container>
</template>
<script>
    import VueUeditorWrap from 'vue-ueditor-wrap'

    export default {
        components: {VueUeditorWrap},
        data() {
            return {
                options: [{
                    value: '0',
                    key: '0',
                    label: '上架'
                }, {
                    value: '1',
                    key: '1',
                    label: '下架'
                }],
                classifyOptions: {},
                pic1Show: false,
                pic2Show: false,
                pic3Show: false,
                previews: {},
                target: '',
                dialogVisible: false,
                // 裁剪组件的基础配置option
                option: {
                    img: '', // 裁剪图片的地址
                    info: true, // 裁剪框的大小信息
                    outputSize: 1, // 裁剪生成图片的质量
                    outputType: 'png', // 裁剪生成图片的格式
                    canScale: false, // 图片是否允许滚轮缩放
                    autoCrop: true, // 是否默认生成截图框
                    // autoCropWidth: 200, // 默认生成截图框宽度
                    // autoCropHeight: 200, // 默认生成截图框高度
                    fixedBox: false, // 固定截图框大小 不允许改变
                    fixed: true, // 是否开启截图框宽高固定比例
                    fixedNumber: [1, 1], // 截图框的宽高比例
                    full: true, // 是否输出原图比例的截图
                    canMoveBox: true, // 截图框能否拖动
                    original: false, // 上传图片按照原始比例渲染
                    centerBox: true, // 截图框是否被限制在图片里面
                    infoTrue: true // true 为展示真实输出图片宽高 false 展示看到的截图框宽高
                },
                picsList: [],  //页面显示的数组
                // 防止重复提交
                loading: false,
                myConfig: {
                    serverUrl: this.axios.defaults.baseURL + '/ueditor/ueditorConfig',
                    UEDITOR_HOME_URL: this.axios.defaults.baseURL + '/ueditor/',
                    // 编辑器不自动被内容撑高
                    autoHeightEnabled: true,
                    // 工具栏是否可以浮动
                    autoFloatEnabled: false,
                    // 初始容器高度
                    initialFrameHeight: 300,
                    // 初始容器宽度
                    initialFrameWidth: '100%',
                    // 关闭自动保存
                    enableAutoSave: false
                },
                ruleForm: {
                    id: '',
                    cover: '',
                    imgSrc1: '',
                    imgSrc2: '',
                    imgSrc3: '',
                    name: '',
                    stock: '',
                    price: '',
                    state: '0',
                    classifyProducts: [],
                    contentHtml: ''
                },
                rules: {
                    cover: [
                        {
                            required: true,
                            message: '请上传商品封面',
                            trigger: 'blur'
                        },
                    ],
                    name: [
                        {
                            required: true,
                            message: '请输入商品名称',
                            trigger: 'blur'
                        },
                    ],
                    classifyProducts: [
                        {
                            required: true,
                            message: '请选择商品分类',
                            trigger: 'blur'
                        },
                    ],
                }
            }
        },
        mounted() {
            this.axios.get('/api/classify/getAll').then((response) => {
                this.classifyOptions = response.data.data;
            }).catch((response) => {
                console.log(response);
            })
            if (this.$route.query.id != undefined) {
                this.axios.get('/api/product/get/' + this.$route.query.id).then((response) => {
                    this.ruleForm.id = response.data.data.id;
                    this.ruleForm.name = response.data.data.name;
                    this.ruleForm.cover = response.data.data.cover;
                    this.ruleForm.state = response.data.data.state + "";
                    this.ruleForm.stock = response.data.data.stock;
                    this.ruleForm.classifyProducts = response.data.data.classifyProducts;

                    console.log(this.ruleForm.state)

                    this.ruleForm.price = response.data.data.price;
                    this.ruleForm.contentHtml = response.data.data.contentHtml;

                    if (response.data.data.imgSrc1) {
                        this.pic1Show = true;
                    }
                    if (response.data.data.imgSrc2) {
                        this.pic2Show = true;
                    }
                    if (response.data.data.imgSrc3) {
                        this.pic3Show = true;
                    }
                    this.ruleForm.imgSrc1 = response.data.data.imgSrc1;
                    this.ruleForm.imgSrc2 = response.data.data.imgSrc2;
                    this.ruleForm.imgSrc3 = response.data.data.imgSrc3;

                }).catch((response) => {
                    console.log(response);
                })
            }
        },
        methods: {
            // 上传按钮
            changeUpload0(file) {
                this.target = 'cover';
                this.changeUpload(file);
            },
            changeUpload1(file) {
                this.target = 'imgSrc1';
                this.changeUpload(file);
            },
            changeUpload2(file) {
                this.target = 'imgSrc2';
                this.changeUpload(file);
            },
            changeUpload3(file) {
                this.target = 'imgSrc3';
                this.changeUpload(file);
            },
            changeUpload(file) {
                //上传图片
                const isIMAGE = (file.raw.type === 'image/jpeg' || file.raw.type === 'image/png' || file.raw.type === 'image/gif');
                if (!isIMAGE) {
                    this.$message({
                        type: 'info',
                        message: '上传文件只能是图片格式!'
                    });
                    return false;
                }
                var reader = new FileReader();
                reader.readAsDataURL(file.raw);
                let that = this;
                reader.onload = function () {
                    if (that.target === 'cover') {
                        that.option.fixedNumber = [1, 1];
                    } else {
                        that.option.fixedNumber = [144, 36];

                        if (that.target == 'imgSrc1') {
                            that.pic1Show = true;
                        }
                        if (that.target == 'imgSrc2') {
                            that.pic2Show = true;
                        }
                        if (that.target == 'imgSrc3') {
                            that.pic3Show = true;
                        }
                    }

                    that.dialogVisible = true;
                    that.option.img = this.result;
                }
            },
            realTime(data) {
                this.previews = data;
            },
            // 点击裁剪，这一步是可以拿到处理后的地址
            finish() {
                this.$refs.cropper.getCropBlob((data) => {
                    this.loading = true

                    let formData = new FormData()
                    formData.append('file', data);

                    let config = {
                        headers: {'Content-Type': 'multipart/form-data'}
                    }
                    // 添加请求头
                    this.axios.post('/api/upload/file', formData, config).then(response => {
                        if (response.data.state == 0) {
                            this.dialogVisible = false;
                            this.ruleForm[this.target] = response.data.data;
                            this.loading = false;
                        } else {
                            this.loading = false;
                            this.$message({
                                type: 'info',
                                message: this.$t('common.errorTip')
                            });
                        }
                    })
                })
            },
            submitForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.axios.post('/api/product/save', {
                            ...this.ruleForm
                        }).then((response) => {
                            if (response.data.state == '0') {
                                this.back();
                            } else {
                                this.$message({
                                    type: 'info',
                                    message: '保存失败'
                                });
                            }
                        }).catch((response) => {
                            console.log(response);
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            back() {
                this.$router.push({path: '/productManage/index', replace: true});
            }
        }
    }
</script>

<style>
    .demo-ruleForm {
        width: 100%;
    }

    .formDiv {
        display: flex;
    }

    .el-form-item__content {
        line-height: 20px;
    }

    .pic1 {
        width: 420px;
        height: 197px;
    }

    .pic {
        max-width: 200px;
        max-height: 200px;
    }

    .cropper-content {
        width: auto;
        height: 400px;
    }

    .cropper {
        width: auto;
        height: 400px;
    }

</style>
